:root {
  --bg-a: #f0fdf4;
  --bg-b: #ecfeff;
  --card: #ffffff;
  --accent: #10b981;
  --accent-dark: #059669;
  --muted: #6b7280;
}

/* ===== Marketplace Container ===== */
.marketplace-page {
  min-height: 100vh;
  background: linear-gradient(180deg, var(--bg-a) 0%, var(--bg-b) 100%);
  background-image: radial-gradient(circle at 10% 20%, rgba(16, 185, 129, 0.04), transparent 6%), radial-gradient(circle at 90% 80%, rgba(6, 95, 70, 0.03), transparent 6%), linear-gradient(180deg, var(--bg-a) 0%, var(--bg-b) 100%);
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  font-family: 'Inter', 'Segoe UI', system-ui, -apple-system, Arial, sans-serif;
  color: #0f172a;
}

.market-header {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0.03));
  padding: 0.9rem 1rem;
  color: var(--accent-dark);
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(2, 6, 23, 0.06);
  border: 1px solid rgba(15, 23, 42, 0.03);
  -webkit-backdrop-filter: blur(6px);
  backdrop-filter: blur(6px);
}

.header-inner {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.market-header h1 {
  margin: 0;
  font-size: 1.6rem;
  color: var(--accent-dark);
  font-weight: 800;
}

.header-actions {
  display: flex;
  gap: 0.8rem;
  align-items: center;
}

.search-input {
  padding: 0.6rem 0.9rem;
  border-radius: 10px;
  border: 1px solid rgba(15, 23, 42, 0.06);
  min-width: 220px;
}

.cart-toggle {
  background: linear-gradient(90deg, var(--accent) 0%, var(--accent-dark) 100%);
  color: white;
  padding: 0.6rem 0.95rem;
  border-radius: 10px;
  font-weight: 800;
  border: none;
  cursor: pointer;
  box-shadow: 0 8px 20px rgba(16, 185, 129, 0.14);
}

.market-main {
  display: grid;
  grid-template-columns: 220px 1fr 360px;
  gap: 1.25rem;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.filters {
  background: var(--card);
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(2, 6, 23, 0.06);
  border: 1px solid rgba(15, 23, 42, 0.03);
}

.filter-btn {
  display: block;
  width: 100%;
  padding: 0.6rem;
  margin: 0.4rem 0;
  text-align: left;
  background: transparent;
  border: none;
  cursor: pointer;
  border-radius: 8px;
}

.filter-btn.active {
  background: linear-gradient(90deg, rgba(16, 185, 129, 0.08), rgba(16, 185, 129, 0.04));
  color: #065f46;
  font-weight: 800;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 1rem;
}

.product-card {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.98), rgba(250, 250, 252, 0.95));
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 12px 36px rgba(2, 6, 23, 0.06);
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  border: 1px solid rgba(15, 23, 42, 0.03);
}

.p-emoji {
  font-size: 2.8rem;
  text-align: center;
}

.p-desc {
  color: var(--muted);
  font-size: 0.92rem;
  margin: 0;
  min-height: 44px;
}

.p-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.p-price {
  color: var(--accent-dark);
  font-weight: 900;
}

.add-btn {
  background: linear-gradient(90deg, var(--accent) 0%, var(--accent-dark) 100%);
  color: white;
  padding: 0.45rem 0.8rem;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  box-shadow: 0 8px 20px rgba(16, 185, 129, 0.12);
}

/* ===== Cart Sidebar ===== */
.cart-sidebar {
  background: var(--card);
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 14px 40px rgba(2, 6, 23, 0.08);
  transform: translateX(110%);
  transition: transform 0.35s ease;
  position: relative;
  border: 1px solid rgba(15, 23, 42, 0.03);
}

.cart-sidebar.open {
  transform: translateX(0);
}

.cart-item {
  display: flex;
  justify-content: space-between;
  gap: 0.8rem;
  align-items: center;
  padding: 0.6rem 0;
  border-bottom: 1px dashed rgba(15, 23, 42, 0.04);
}

.ci-left {
  display: flex;
  gap: 0.6rem;
  align-items: center;
}

.ci-emoji {
  font-size: 1.6rem;
}

.ci-info strong {
  display: block;
}

.ci-info span {
  font-size: 0.85rem;
  color: var(--muted);
}

.ci-right {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.ci-right input {
  width: 60px;
  padding: 0.35rem;
  border-radius: 6px;
  border: 1px solid rgba(15, 23, 42, 0.06);
}

.remove {
  background: #fee2e2;
  border: none;
  padding: 0.3rem 0.5rem;
  border-radius: 6px;
  cursor: pointer;
}

.cart-summary {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
}

.checkout-btn {
  background: linear-gradient(90deg, var(--accent) 0%, var(--accent-dark) 100%);
  color: white;
  padding: 0.6rem;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 800;
  box-shadow: 0 12px 30px rgba(16, 185, 129, 0.12);
}

.empty {
  color: var(--muted);
}

/* ===== Responsive ===== */
@media (max-width: 1024px) {
  .market-main {
    grid-template-columns: 1fr 340px;
  }

  .filters {
    display: none;
  }
}

@media (max-width: 768px) {
  .market-header h1 {
    font-size: 1.3rem;
  }

  .search-input {
    min-width: 150px;
  }
}

@media (max-width: 640px) {
  .market-main {
    grid-template-columns: 1fr;
  }

  .cart-sidebar {
    position: fixed;
    right: 0;
    top: 0;
    height: 100vh;
    width: 85vw;
    /* Wider on mobile for better usability */
    max-width: 350px;
    z-index: 2000;
    box-shadow: -5px 0 25px rgba(0, 0, 0, 0.15);
    border-radius: 20px 0 0 20px;
    padding-top: 80px;
    /* Space for header if z-index issue, otherwise adjust */
    overflow-y: auto;
  }

  /* Add overlay when cart is open (handled by JS usually, but here just styling sidebar) */

  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    /* Smaller cards on mobile */
    gap: 0.8rem;
  }

  .product-card {
    padding: 0.8rem;
  }

  .p-emoji {
    font-size: 2.2rem;
  }

  .header-inner {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .header-actions {
    justify-content: space-between;
  }

  .search-input {
    flex: 1;
    min-width: 0;
  }
}