}

/* ===== No Products State ===== */
.no-products {
  text-align: center;
  padding: 3rem 2rem;
  color: #9ca3af;
}

.no-products p {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}

.add-first-btn {
  padding: 0.8rem 1.5rem;
  background: #f59e0b;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-first-btn:hover {
  background: #d97706;
  transform: scale(1.05);
}

