  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  padding: 1rem;
  background: white;
  border-radius: 8px;
  gap: 1rem;
}

.price,
.quantity {
  flex: 1;
  text-align: center;
  font-weight: 600;
  color: #f59e0b;
}

