  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.delete-btn:hover {
  background: #fca5a5;
  transform: scale(1.02);
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  .sell-header h1 {
    font-size: 1.8rem;
  }

  .header-content {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .tab-navigation {
    flex-direction: column;
  }

  .tab-btn {
    width: 100%;
  }

  .image-selector {
    grid-template-columns: repeat(auto-fill, minmax(45px, 1fr));
  }

  .products-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .sell-page {
    padding: 1rem 0.5rem;
  }

  .sell-header {
    padding: 1rem;
    margin-bottom: 1rem;
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .header-content {
    flex-direction: column;
    gap: 1rem;
  }

  .sell-header h1 {
    font-size: 1.4rem;
  }

  .sell-brand {
    justify-content: center;
  }

  .add-product-section,
  .my-products-section {
    padding: 1.5rem 1rem;
    border-radius: 12px;
  }

  .stats-section {
    grid-template-columns: 1fr;
    /* Stack stats on very small screens */
    gap: 0.8rem;
  }

  .stat-card {
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }

  .stat-card h3 {
    margin-bottom: 0;
  }

  .product-form {
    gap: 1rem;
  }

  /* Make grid 1 column on mobile but with reasonable card size */
  .products-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .product-card {
    flex-direction: row;
    /* Horizontal card on mobile for compactness */
    align-items: stretch;
  }

  .product-image {
    width: 80px;
    min-height: auto;
    font-size: 2.5rem;
    padding: 0.5rem;
  }

  .product-info {
    padding: 0.8rem;
  }

  .product-info h3 {
    font-size: 1rem;
  }

  .product-details {
    padding: 0.5rem;
    font-size: 0.9rem;
  }

  .image-selector {
    grid-template-columns: repeat(5, 1fr);
    gap: 0.5rem;
  }

  .emoji-btn {
    padding: 0.4rem;
    font-size: 1.5rem;
  }
}